<template>
  <article id="works">
    <section class="ttlArea" v-scroll="ttlScroll" v-observe-visibility="{callback: visibilityChangedTtl, once: true,}" v-bind:class='{active:isVisibleTtl}'>
      <h1 v-observe-visibility="{callback: visibilityChangedTtl, once: true,}" v-bind:class='{active:isVisibleTtl}'>WORKS</h1>
    </section>
    <div class="bgWhite">
      <section class="worksArea">
        <div class="tabs">
          <p class="tabsFirst">WEB SITE</p>
          <router-link to="/worksmovie/">WEDDING MOVIE</router-link>
        </div>
        <WorksBlock></WorksBlock>
      </section>
    </div>
  </article>
</template>

<script>
import WorksBlock from '../components/WorksBlock.vue'
export default {
  name: 'Works',
  data(){
    return {
      isVisibleTtl: false,
      activetab: 1,
      title: 'WORKS'
    }
  },
  methods: {
    visibilityChangedTtl(isVisibleTtl) {
      this.isVisibleTtl = isVisibleTtl
    },
    ttlScroll: function (evt, el) {
      if (window.innerWidth < 768) {
        if (window.scrollY > 366) {
          el.setAttribute(
            'style',
            'display: none;'
          )
        } else{
          el.setAttribute(
            'style',
            'display: flex;'
          )
        }
      }
    }
  },
  components: {
    WorksBlock
  },
  head: {
    title: function () {
      return {
        inner: this.title
      }
    }
  }
}
</script>